 /* MAIN HEADER */
  #index-header {
    display: none;
  }


  #mobile-header {
    position: sticky;
    top: 0;
    z-index: 10;
    width: 100%;
    border-bottom: solid 4px var(--main-color);
    display: block;

}

#mobile-nav {
  background: linear-gradient(
    to right,
    var(--main-color),
    var(--third-color)
  );
  width: 100%;
}

#mobile-nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  height: 70px;
  box-sizing: border-box;
}

#mobile-logo {
  height: 100%;
  display: flex;
  align-items: center;

}

#mobile-logo img {
  height: 100%;
  border: solid white 2px;
  transition: 0.1s;
  width: 130px;
}

#mobile-logo img:hover {
  border-color: var(--secondary-color);
}

#mobile-menu-btn {
  font-size: 2rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

#mobile-menu {
  background-color: var(--third-color);
  height: 100vh;
  width: 99%;
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  border: solid var(--fourth-color) 2px;
  margin-left: 0.5%;
  overflow: hidden;
display: none;
}

#mobile-menu-close{
  width: 100%;
  font-size: 30px;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 3%;
  box-sizing: border-box;
  cursor: pointer;
  height: 7vh;
  margin-bottom: 0;

}

#mobile-menu-links{

  margin-top: 0;
  list-style: none;
  width: 100%;
  height: 63%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0;
  margin-bottom: 0;
}

.mobile-menu-links-li {
  border-bottom: solid var(--fourth-color) 1.5px;
  width: 100%;
  cursor: pointer;
  height: 12.5%;
}

.mobile-menu-links-li-a{
  text-decoration: none;
  color: var(--fourth-color);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#mobile-menu-links-selected{
  background-color: var(--fourth-color);
}
#mobile-menu-links-selected a {
  color: var(--third-color);
}

#mobile-menu-info{
  margin-top: 0;
  background-color: black;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.mobile-menu-info{
  width: 100%;
  height: 22%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
}

.mobile-menu-info-div1{
  height: 100%;
  min-width: 30%;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 2%;
  box-sizing: border-box;
}

.mobile-menu-info-div1 h4 {
  color: white;
  margin-left: 4%;
}

.mobile-menu-info-anchor{

  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 1%;
  overflow-x: scroll;
  text-decoration: none;
  color: white;

}

#mobile-menu-info-social{
  border: solid white 2px;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  margin-top: 0;
  display: flex ;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

.mobile-menu-info-social-li{
  height: 100%;
  color: var(--secondary-color);
  margin-left: 1%;
}


.mobile-menu-info-social-li2{
  height: 100%;
  margin-left: 3%;
}

.mobile-menu-info-social-li2-a{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-menu-info-social-li2-img{
  height:  20px;
  width: 20px;
}






/* main section  */

#index-main{
  display: none;
}

#index-main-mobile {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Arial, sans-serif;
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 0 5%;
  box-sizing: border-box;
  position: relative;
  padding-top: 2%;
}

.mobile-tabs-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  position: relative;
  background-image: url('../../../src/assets/images/index-background-img/background.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: white;
  height: 65vh;
  margin-bottom: 3%;
}

/* Overlay for readability */
.mobile-tabs-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(7,12,43,0.6);
  border-radius: 10px;
  z-index: 0;
}

.mobile-tabs {
  display: flex;
  justify-content: flex-start;
  min-width: 110%;
  z-index: 1; /* Ensure above overlay */
  position: relative;
  margin-bottom:0;
  background-color: rgba(7,12,43,0.9);

  height: 20%;
  margin-top: 2%;
}

.mobile-tab {
  cursor: pointer;
  padding: 3%;
  border-radius: 2px;
  width: 35%;
  margin-left: 1.5%: ;
  background-color: rgba(0,0,0,0.8);
  color: var(--fourth-color);
  border: solid var(--fourth-color) 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
}

.mobile-tab.active {
  background-color: white;
  color: var(--third-color);
}

.mobile-tab:hover {
  background-color: var(--fourth-color);
    border: solid var(--white) 1px;
    color: var(--third-color);
}

.mobile-card {
  width: 100%;
  background-color: rgba(0,0,0,0.9);
  color: white;
  padding: 1rem;

  display: none;
  flex-direction: column;
  position: relative;
  z-index: 1;
  font-size: 16px;
  height:80%;
  overflow-y: scroll;

 margin-bottom: 0;
}
.mobile-card::-webkit-scrollbar {
  width: 8px;
  
}
/* scroll bar thumb (the actual scroll) */
.mobile-card::-webkit-scrollbar-thumb {
  background: var(--fourth-color); /* scrollbar color */
}
/* scrroll bar track */
.mobile-card::-webkit-scrollbar-track {
  background: var(--third-color) /* track color */
}

.mobile-card.active {
  display: flex;
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  /* Styles for small screens (e.g., mobile devices) */
  .mobile-card{

    height: 50vh;
    box-sizing: border-box;
    padding-top: 10%;
  }
  .mobile-tabs-section {
    height: 50vh;
  }
}

.mobile-card h3 {
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.mobile-card p {
  font-size:16px;
  line-height: 1.5;
}



.mobile-card li {
  line-height: 25px;
}

.mobile-links {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;

}

.mobile-links a {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5%;
  background-color: rgba(7, 12, 43,0.7);
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: all 0.3s ease;
  border: solid var(--fourth-color) 1px;
  font-size: 15px;
}

.mobile-links a:hover {
  background-color: var(--fourth-color);
  border: solid white 1px;
  color: var(--third-color);
}

.mobile-maps {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.mobile-maps iframe {
  width: 45%;
  height: 200px;
  border: 2px solid var(--third-color);
  border-radius: 10px;
}
.mobile-maps iframe:hover{
  border: 2px solid var(--fourth-color);
}

.mobile-newsletter {
  width: 100%;
  background-color: var(--main-color);
  color: white;
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  text-align: center;
}

.mobile-newsletter input {
  width: 70%;
  padding: 0.5rem;
  border: none;
  border-radius: 5px;
  margin-right: 0.5rem;
}

.mobile-newsletter button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 5px;
  background-color: var(--secondary-color);
  color: white;
  cursor: pointer;
}

.mobile-newsletter button:hover {
  background-color: var(--fourth-color);
}

#mobile-newsletter-status{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
  margin-top: 2%;
}

#mobile-email-success{
  padding: 2%;
  color: white;
  background-color: green;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#mobile-email-err{
  padding: 2%;
  color: white;
  background-color: red;
  border-radius: 5px;
    display: flex;
  justify-content: center;
  align-items: center;
}

